import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useAuth } from "./AuthContext";
import "bootstrap/dist/css/bootstrap.min.css";

//  
const NavigationBar = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const navigate = useNavigate(); // useNavigate  
  const { isLoggedIn, logout, role } = useAuth(); // useAuth     logout  

  //   
  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const handleLoginLogout = () => {
    logout(); //  
    navigate("/"); //   
  };

  const handleLogoClick = () => {
    navigate("/dashboard");
  }

    return (
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark fixed-top">
          <div className="container mt-1 mb-1 py-0">
            <span className="navbar-brand"  onClick={handleLogoClick} style={{ cursor: "pointer" }}>
              <h1 className="text-dark fw-bold">
                WEAVUS
              </h1>
            </span>
            <button className="navbar-toggler " type="button" onClick={toggleMenu}>
              <span className="navbar-toggler-icon" style={{ filter: "invert(1)" }} ></span>
            </button>
            <div className={`collapse navbar-collapse ${isMenuOpen ? "show" : ""}`} id="navbarNav">
              <ul className="navbar-nav me-auto ">
                {isLoggedIn && role === "ROLE_ADMIN" && (
                    <>
                      <li className="nav-item">
                        <Link className="nav-link text-dark fw-bold "
                              to="/workSchedule/dashBoard">()</Link>
                      </li>
                      <li className="nav-item">
                        <Link className="nav-link
                                         text-dark
                                         fw-bold
                                         "
                              to="/personnel/dashboard"></Link>
                      </li>
                      <li className="nav-item">
                        <Link className="nav-link
                                         text-dark
                                         fw-bold
                                         "
                              to="/dashboard"> </Link>
                      </li>
                      <li className="nav-item">
                        <Link className="nav-link
                                         text-dark
                                         fw-bold
                                         "
                              to="/year/dashboard"> </Link>
                      </li>
                      <li className="nav-item">
                        <Link className="nav-link
                                         text-dark
                                         fw-bold
                                         "
                              to="/employee"> </Link>
                      </li>
                      <li className="nav-item" >
                        <Link className="nav-link
                                         text-dark
                                         fw-bold
                                         "
                              to="/admin"></Link>
                      </li>
                    </>
                )}
                {isLoggedIn && role === "ROLE_GENERAL" && (
                    <div>
                      <li className="nav-item">
                        <Link className="nav-link text-dark fw-bold "
                              to="/jobSchedule/dashboard">()</Link>
                      </li>
                    </div>
                    )}
              </ul>
              <div className="d-flex justify-content-end ">
              <button className={`btn right ${isLoggedIn ? "btn-outline-light text-dark fw-bold" : "btn-primary text-white fw-bold"}` } onClick={handleLoginLogout}>
                {isLoggedIn ? "" : ""}
              </button>
              </div>
            </div>
          </div>
        </nav>
  );
};
