import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import createAxiosInstance from "../../config/api";
import "../../config/index.css";
import {InstitutionListApi} from "../utils/InstitutionListApi";
import {FileUploadDownload} from "./PersonnelFile";

function PersonnelDetail() {
  const { Id } = useParams();
  const navigate = useNavigate();
  const [item, setItem] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [isEditing, setIsEditing] = useState(false);
  const [editedItem, setEditedItem] = useState({
    applicantFile: [], //    
  });
  const { loadList, institutionList} = InstitutionListApi();

  const {
    uploading, progress, downloading ,handleUpload, handleDownload, handleFileChange, files, fileDelete
  } = FileUploadDownload(editedItem, setEditedItem);



  useEffect(() => {
    const fetchPersonnel = async () => {
      try {
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.get(`/personnel/applicant/${Id}`);
        setItem(response.data);
        setEditedItem(response.data);
      } catch (err) {
        setError("   .");
        console.error(err);
      } finally {
        setLoading(false);
      }
    };

    fetchPersonnel();
  }, [Id]);

  useEffect(() => {
    loadList();
  }, [loadList]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setEditedItem((prevItem) => ({
      ...prevItem,
      [name]: value,
    }));
  };

  const handleSave = async (e) => {
    e.preventDefault(); //   
    const confirmSave = window.confirm("  ?");
    if (!confirmSave) {
      return;
    } else {
      try {
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.put(
          `/personnel/applicant/${Id}`,
          editedItem
        );
        setItem(response.data);
        setIsEditing(false);
        window.alert("  ");
      } catch (err) {
        if (err.response && err.response.status === 404) {
          // 404   
          window.alert("     ");
        } else {
          //    
          setError("    .");
        }
      }
    }
  };

  const handleEditClick = () => {
    setIsEditing(true);
  };

  const handleGoBack = () => {
    navigate(-1);
  };

  const handleDelete = async (id) => {
      const confirmDelete = window.confirm(" ?");
      if (!confirmDelete) return;
    try {
      const axiosInstance = createAxiosInstance(); //  
      const response = await axiosInstance.delete(
          `/personnel/applicant/${id}`
      );
      if (response.status === 200) {
        alert("  .");
        navigate("/personnel/dashboard"); //      
      } else if (response.status === 404) {
        alert("   .");
      }
    } catch (err) {
      alert("  .");
      console.error(err);
    }
  };


  if (loading) return <p>Loading...</p>;
  if (error) return <p>{error}</p>;

  return (
      <div className="detail-container mt-4">
        {item && (
            <div className="card">
              <div className="card-header">
                <h3> {item.name}  </h3>
              </div>
              <div className="card-body">

                {isEditing ? (
                    <div>
                      <div>
                        <input
                            className="form-control mb-2"
                            name="id"
                            value={editedItem.id}
                            onChange={handleChange}
                            disabled
                            hidden
                        />
                      </div>
                      <div className="form-group mb-2">
                        <label className="label"> </label>
                        <input
                            className="form-control"
                            name="name"
                            value={editedItem.name}
                            onChange={handleChange}
                            required
                        />
                      </div>
                      <div className="form-group mb-2">
                        <label className="label"></label>
                        <select
                            className="form-control"
                            name="gender"
                            value={editedItem.gender}
                            onChange={handleChange}
                            required
                        >
                          <option value="" disabled>
                              
                          </option>
                          <option value=""></option>
                          <option value=""></option>
                        </select>
                      </div>
                      <div className="form-group mb-2">
                        <h5>email</h5>
                        <input
                            className="form-control"
                            type="email"
                            name="email"
                            value={editedItem.email}
                            onChange={handleChange}
                            required
                        />
                      </div>
                      <div className="form-group mb-2">
                        <h5></h5>
                        <input
                            type="date"
                            className="form-control"
                            name="birthDate"
                            value={editedItem.birthDate || ""}
                            onChange={handleChange}
                            required
                        />
                      </div>
                      <div className="form-group mb-2">
                        <h5></h5>
                        <input
                            type="tel"
                            className="form-control"
                            name="phoneNumber"
                            value={editedItem.phoneNumber}
                            onChange={handleChange}
                        />
                      </div>
                      <div className="form-group mb-2">
                        <h5> </h5>
                        <select
                            className="form-control"
                            name="institutionId"
                            value={editedItem.institutionId || ""}
                            onChange={handleChange}
                            required
                        >
                          <option value="" disabled>
                               
                          </option>
                          {institutionList && institutionList.length > 0 ? (
                              institutionList.map((institution) => (
                                  <option key={institution.id} value={institution.id}>
                                    {institution.name}
                                  </option>
                              ))
                          ) : (
                              <option value="" disabled>
                                Loading...
                              </option>
                          )}
                        </select>
                      </div>
                      <div className="form-group mb-2">
                        <h5> </h5>
                        <input
                            type="date"
                            className="form-control"
                            name="joiningDate"
                            value={editedItem.joiningDate || ""}
                            onChange={handleChange}
                            required
                        />
                      </div>
                      <div className="form-group mb-2">
                        <h5> </h5>
                        <select
                            className="form-control"
                            name="admissionStatus"
                            value={editedItem.admissionStatus}
                            onChange={handleChange}
                            required
                        >
                          <option value="" disabled>
                               .
                          </option>
                          <option value=""></option>
                          <option value=""></option>
                          <option value=""></option>
                          <option value=""></option>
                          <option value=""></option>
                          <option value=""></option>
                          <option value=""></option>
                        </select>
                      </div>
                      <div className="form-group mb-2">
                        <h5> </h5>
                        <textarea
                            className="form-control  my-3 mt-1"
                            name="log"
                            value={editedItem.log}
                            onChange={handleChange}
                            rows="10"
                            style={{ whiteSpace: 'pre-wrap' }}
                        />
                      </div>
                      <div>
                        <h5> </h5>
                        <div>
                          <input
                              className="form-control "
                              id="file-upload-type2"
                              type="file"
                              accept=".jpg,.png,.pdf" //    
                              onChange={(e) => handleFileChange(e, "resumeFileName1")}
                          />
                          <div>
                            {["resumeFileName1"].map((resumeType, index) => {
                              const file = editedItem.applicantFile?.find((file) => file.resumeType === resumeType);

                              if (file) {
                                return (
                                    <div key={index} style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                                      <span>{file.fileName}</span>
                                      <button className="btn btn-primary my-3" onClick={() => fileDelete(file.id)}></button>
                                    </div>
                                );
                              }
                              return <p key={index}> .</p>;
                            })}
                          </div>
                        </div>
                        <div>
                          <input
                              id="file-upload-type2"
                              className="form-control"
                              type="file"
                              accept=".jpg,.png,.pdf"
                              onChange={(e) => handleFileChange(e, "resumeFileName2")} //   
                          />
                          <div>
                            {["resumeFileName2"].map((resumeType, index) => {
                              const file = editedItem.applicantFile?.find((file) => file.resumeType === resumeType);
                              if (file) {
                                return (
                                    <div key={index} style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                                      <span>{file.fileName}</span>
                                      <button className="btn btn-primary my-3" onClick={() => fileDelete(file.id)}></button>
                                    </div>
                                );
                              }
                              return <p key={index}> .</p>;
                            })}
                          </div>
                        </div>
                        <div>
                          <input
                              id="file-upload-type3 "
                              className="form-control"
                              type="file"
                              accept=".jpg,.png,.pdf"
                              onChange={(e) => handleFileChange(e, "resumeFileName3")} //   
                          />
                          <div>
                            {["resumeFileName3"].map((resumeType, index) => {
                              const file = editedItem.applicantFile?.find(
                                  (file) => file.resumeType === resumeType
                              );
                              if (file) {
                                return (
                                    <div key={index} style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                                      <span>{file.fileName}</span>
                                      <button className="btn btn-primary my-3" onClick={() => fileDelete(file.id)}></button>
                                    </div>
                                );
                              }
                              return <p key={index}> .</p>;
                            })}
                          </div>
                        </div>
                        <div style={{ marginTop: "20px" }}>
                          {uploading && (
                              <div style={{ marginBottom: "10px" }}>
                                <p>  : {progress}%</p>
                                <div
                                    style={{
                                      width: "100%",
                                      backgroundColor: "#e0e0e0",
                                      borderRadius: "5px",
                                    }}
                                >
                                  <div
                                      style={{
                                        width: `${progress}%`,
                                        backgroundColor: "#4caf50",
                                        height: "10px",
                                        borderRadius: "5px",
                                      }}
                                  ></div>
                                </div>
                              </div>
                          )}
                          <button
                              onClick={handleUpload}
                              disabled={uploading || files.length === 0}
                              className="btn btn-primary"
                          >
                            {uploading ? " ..." : ""}
                          </button>
                        </div>
                      </div>
                    </div>
                ) : (
                    <div className="card-body">
                      <div className="form-group">
                        <label className="label"> </label>
                        <p className="box">{item.name}</p>
                      </div>
                      <div className="form-group">
                        <label className="label"></label>
                        <p className="box">{item.gender}</p>
                      </div>
                      <div className="form-group mb-2">
                        <label className="label">email</label>
                        <p className="box">{item.email}</p>
                      </div>
                      <div className="form-group mb-2">
                        <label className="label"></label>
                        <p className="box">{item.birthDate}</p>
                      </div>
                      <div className="form-group mb-2">
                        <label className="label"></label>
                        <p className="box">{item.phoneNumber}</p>
                      </div>
                      <div className="form-group mb-2">
                        <label className="label"> </label>
                        <p className="box">{item.institution.name}</p>
                      </div>
                      <div className="form-group mb-2">
                        <label className="label"> </label>
                        <p className="box">{item.joiningDate}</p>
                      </div>
                      <div className="form-group mb-2">
                        <label className="label"> </label>
                        <p className="box">{item.admissionStatus}</p>
                      </div>
                      <div className="form-group mb-2">
                        <label className="label"> </label>
                        <p className="box" style={{ whiteSpace: 'pre-wrap' }}>{item.log}</p>
                      </div>
                      <div className="form-group mb-2">
                        <label className="label"> </label>
                        <div >
                          <button
                              onClick={handleDownload}
                              disabled={downloading}
                              className="btn btn-primary"
                          >
                            {downloading ? " ..." : ""}
                          </button>
                        </div>
                      </div>
                    </div>
                )}
              </div>
              <div className="card-footer d-flex justify-content-between">
                {isEditing ? (
                    <button
                        onClick={handleSave}
                        className="btn btn-success"
                    >
                      
                    </button>
                ) : (
                    <button
                        onClick={handleEditClick}
                        className="btn btn-primary"
                    >
                      
                    </button>
                )}
              </div>
              <div>
                {!isEditing && (
                    <button
                        onClick={() => handleDelete(item.id)}
                        className="btn btn-danger"
                    >
                      
                    </button>
                )}
                <button
                    onClick={handleGoBack}
                    className="btn btn-secondary"
                >
                  
                </button>
              </div>
            </div>
        )}
      </div>
  );
}
