
import React, {useEffect, useState} from 'react';
import {useNavigate} from "react-router-dom";
import 'bootstrap-icons/font/bootstrap-icons.css';
import "../../config/index.css";

function WorkScheduleList () {

        const today = new Date();
        const [year, setYear] = useState(today.getFullYear());
        const [month, setMonth] = useState(today.getMonth() + 1);
        const [schedule, setSchedule] = useState([]);
        const navigate = useNavigate();
        const daysInMonth = new Date(year, month, 0).getDate();
    const holidayData = {
        "1-1": "",
        "2-11": "",
        "4-29": "",
        "5-3": "",
        "5-4": "",
        "5-5": "",
        "11-3": "",
        "11-23": ""
        //   
    };

    const dummyData = {
        "1-1": { attendanceType: "", workType: "", checkInTime: "", checkOutTime: "", memo: "" },
        "1-2": { attendanceType: "", workType: "", checkInTime: "09:00", checkOutTime: "18:00", memo: " " },
        "1-3": { attendanceType: "", workType: "", checkInTime: "", checkOutTime: "", memo: " " },
        "1-6": { attendanceType: "", workType: "", checkInTime: "09:00", checkOutTime: "18:00", memo: "" },
    };

    const newSchedule = Array.from({ length: daysInMonth }, (_, i) => {
        const key = `${month}-${i + 1}`;
        const weekday = new Date(year, month - 1, i + 1).toLocaleDateString("ja-JP", { weekday: "short" });
        const isWeekend = weekday === "" || weekday === ""; //  
        const isHoliday = holidayData[key] !== undefined; //   (  )

        return {
            date: `${i + 1}`,
            weekday,
            isHoliday,
            attendanceType: dummyData[key]?.attendanceType || "",
            workType: dummyData[key]?.workType || "",
            checkInTime: dummyData[key]?.checkInTime || "",
            checkOutTime: dummyData[key]?.checkOutTime || "",
            memo: dummyData[key]?.memo || "",
            styleClass : isHoliday ? "holiday" : isWeekend ? "weekend" : "" //    
        };
    });

        useEffect(() => {
            generateSchedule(year, month);
        }, [year, month]);

        const generateSchedule = (year, month) => {
            setSchedule(newSchedule);
        };

        const handleInputChange = (index, field, value) => {
            const newSchedule = [...schedule];
            newSchedule[index][field] = value;
            setSchedule(newSchedule);
        };


    const changeMonth = (direction) => {
        setMonth((prev) => {
            let newMonth = prev + direction;
            let newYear = year;

            if (newMonth < 1) {
                newYear -= 1;
                newMonth = 12;
            } else if (newMonth > 12) {
                newYear += 1;
                newMonth = 1;
            }

            setYear(newYear);
            return newMonth;
        });
    };

    const handleClickMypage = () => {
        const scheduleData  = {stateInfo : "true"};
        navigate("/workSchedule/dashBoard", {state: scheduleData });
    }

    const handleClickEdit = (Id) => {
        navigate(`/workSchedule/detail/${Id}`);
    }



        return (
            <div className="container">
                <h3 className="text-dark mb-1">WORK SCHEDULE</h3>
                <div className="d-flex justify-content-center align-items-center">
                    <button onClick={() => changeMonth(-1)}></button>
                    <h2 className="px-3">{year} / {month.toString().padStart(2, '0')}</h2>
                    <button onClick={() => changeMonth(1)}></button>
                </div>
                <button type="button" className="action-button" onClick={handleClickMypage}>
                      
                </button>
                <div className="table-responsive">
                    <table className="table table-striped">
                        <thead>
                        <tr>
                            <th className="text-center"></th>
                            <th className="text-center"></th>
                            <th className="text-center"></th>
                            <th className="text-center"></th>
                            <th className="text-center"></th>
                            <th className="text-center"></th>
                            <th className="text-center"></th>
                        </tr>
                        </thead>
                        <tbody>
                        {schedule.map((day, index) => (
                            <tr key={index} >
                                <td className={day.styleClass}>
                                    {day.date} &nbsp;
                                        <i className="bi bi-pencil-fill"
                                           onClick={() => handleClickEdit(day.date)}
                                        >
                                        </i>
                                </td>
                                <td className={day.styleClass}>{day.weekday}</td>
                                <td className={day.styleClass}>{day.attendanceType}</td>
                                <td className={day.styleClass}>
                                    {day.workType}
                                </td>
                                <td className={day.styleClass}>{day.checkInTime}</td>
                                <td className={day.styleClass}>{day.checkOutTime}</td>
                                <td className={day.styleClass}>{day.memo}</td>

                            </tr>
                        ))}
                        </tbody>
                    </table>
                </div>
            </div>
        );
}
export default WorkScheduleList;