import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import createAxiosInstance from "../../config/api";
import "../../config/index.css";

function PersonnelInstitutionDetail() {
  const { Id } = useParams();
  const navigate = useNavigate();
  const [item, setItem] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [isEditing, setIsEditing] = useState(false);
  const [editedItem, setEditedItem] = useState({});


  useEffect(() => {
    const fetchPersonnel = async () => {
      try {
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.get(`/personnel/institution/${Id}`);
        setItem(response.data);
        setEditedItem(response.data);
      } catch (err) {
        setError("    .");
        console.error(err);
      } finally {
        setLoading(false);
      }
    };

    fetchPersonnel();
  }, [Id]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setEditedItem((prevItem) => ({
      ...prevItem,
      [name]: value,
    }));
  };

  const handleSave = async (e) => {
    e.preventDefault(); //   
    const confirmSave = window.confirm("    ?");
    if (!confirmSave) {
      return;
    } else {
      try {
        const axiosInstance = createAxiosInstance(); //  
        const response = await axiosInstance.put(
          `/personnel/institution/${Id}`,
          editedItem
        );
        setItem(response.data);
        setIsEditing(false);
        window.alert("   ");
        navigate(0);
      } catch (err) {
        if (err.response && err.response.status === 404) {
          // 404   
          window.alert("     ");
        } else {
          //    
          setError("     .");
        }
      }
    }
  };

  const handleEditClick = () => {
    setIsEditing(true);
  };

  const handleGoBack = () => {
    navigate(-1);
  };

  const handleDelete = async (id) => {
      const confirmDelete = window.confirm("  ?");
      if (!confirmDelete) return;
    try {
      const axiosInstance = createAxiosInstance(); //  
      const response = await axiosInstance.delete(
          `/personnel/institution/${id}`
      );
      if (response.status === 200) {
        alert("   .");
        navigate("/personnel/institution/list"); //      
      } else if (response.status === 404) {
        alert("    .");
      }
    } catch (err) {
      alert("   .    .");
      console.error(err);
    }
  };

  const handleGoRegi = (Id) => {
    navigate(`/personnel/institution/${Id}/schedule/new`);
  };

  if (loading) return <p className="">Loading...</p>;
  if (error) return <p style={styles.errorMessage}>{error}</p>;

  return (
    <div className="detail-container mt-4">
      {item && (
        <div className="card">
          <div className="card-header">
            <h3>  {item.name}  </h3>
          </div>
          <div className="card-body">
            {isEditing ? (
              <div>
                <div className="form-group">
                  <label className="label"> </label>
                  <div>
                    <input
                        className="input"
                            type="text"
                           name="name"
                           value={editedItem.name}
                           onChange={handleChange}
                    />
                  </div>
                </div>
                <div className="form-group">
                  <label className="label"> </label>
                  <div>
                    <input
                        className="input"
                        type="email"
                        name="contactInfo"
                        value={editedItem.contactInfo}
                        onChange={handleChange}
                    />
                  </div>
                </div>
                <div className="form-group">
                  <label className="label">  </label>
                  <textarea
                      type="text"
                      name="log"
                      value={editedItem.log}
                      onChange={handleChange}
                      className="input"
                      rows="10"
                      cols="50"
                  />
                </div>
              </div>
            ) : (
              <>
                <div className="form-group">
                  <label className="label"> ID</label>
                  <div>
                    {item.id}
                  </div>
                </div>
                <div className="form-group">
                    <label className="label"> </label>
                    <div className="box">
                      <p>{item.name}</p>
                    </div>
                </div>
                <div className="form-group">
                    <label className="label"> </label>
                    <div className="box">
                        <p>{item.contactInfo}</p>
                    </div>
                </div>
                <div className="form-group">
                    <label className="label">  </label>
                  <div className="box">
                    <ul>
                      {item.applicantNames.map((name, index) => (
                          <li key={index}>{name}</li>
                      ))}
                    </ul>
                  </div>
                </div>
                <div className="form-group">
                  <label className="label"> </label>
                  <div className="box">
                    <p style={{ whiteSpace: 'pre-wrap' }}>{item.log}</p>
                  </div>
                </div>
              </>
            )}
          </div>
          <div className="card-footer">
            {isEditing ? (
                <button className="action-button" onClick={handleSave}>
                  
                </button>
            ) : (
              <button className="action-button" onClick={handleEditClick}>
                
              </button>
            )}
            {!isEditing && (
              <button
                className="action-button"
                onClick={() => handleDelete(item.id)}
              >
                
              </button>
            )}
            {isEditing ? (<> </>) : (
                <button
                    className="action-button"
                    onClick={() => handleGoRegi(item.id)}
                > 
                </button>
            )}
            <button className="action-button" onClick={handleGoBack}>
              
            </button>
          </div>
        </div>
      )}
    </div>

  );
}
