import React, {useEffect, useState} from "react";
import {useLocation, useNavigate} from "react-router-dom";

function WorkScheduleDashboard (){
    const [item, setItem] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState("");
    const [isEditing, setIsEditing] = useState(false);
    const [editedItem, setEditedItem] = useState({
        checkInTime: "00:00",
        checkOutTime: "00:00",
        breakTimeIn: "00:00",
        breakTimeOut: "00:00",
        workType: "",
        attendanceType: "",
        memo: "  "
    });
    const navigate = useNavigate();
    const location = useLocation();
    
    function handleClick() {
        navigate("/workSchedule/list");
    }

    const handleChange = (e) => {
        const { name, value } = e.target;
        setEditedItem((prev) => ({
            ...prev,
            [name]: value
        }));
    };

    return (
            <div className="detail-container mt-4">
                    <div className="card">
                        {/*<form onSubmit={handleClick}>*/}
                            <div className="card-header">
                                <h3>  </h3>
                            </div>
                            <div className="card-body">
                                <div>
                                        <div className="form-group">
                                            <label className="label"> </label>
                                            <div className="d-flex">
                                                <input
                                                    type="time"
                                                    className="input "
                                                    name="checkInTime"
                                                    onChange={handleChange}
                                                    value={editedItem.checkInTime}
                                                />
                                                <h2>
                                                    ~
                                                </h2>
                                                <input
                                                    type="time"
                                                    className="input"
                                                    name="checkOutTime"
                                                    onChange={handleChange}
                                                    value={editedItem.checkOutTime}
                                                />
                                            </div>
                                        </div>
                                    <div className="form-group">
                                        <label className="label"></label>
                                        <div className="d-flex">
                                            <input
                                                type="time"
                                                className="input"
                                                name="breakTimeIn"
                                                onChange={handleChange}
                                                value={editedItem.breakTimeIn}
                                            />
                                            <h2>
                                                ~
                                            </h2>
                                            <input
                                                type="time"
                                                className="input"
                                                name="breakTimeOut"
                                                onChange={handleChange}
                                                value={editedItem.breakTimeOut}
                                            />
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <label className="label"> </label>
                                        <select
                                            name="workType"
                                            value={editedItem.workType}
                                            onChange={handleChange}
                                            className="input"
                                            required
                                        >
                                            <option value="" disabled>
                                                   .
                                            </option>
                                            <option value=""></option>
                                            <option value=""></option>
                                            <option value=""></option>
                                        </select>
                                    </div>
                                    <div className="form-group">
                                        <label className="label"> </label>
                                        <select
                                            name="attendanceType"
                                            value={editedItem.attendanceType}
                                            onChange={handleChange}
                                            className="input"
                                            required
                                        >
                                            <option value="" disabled>
                                                   .
                                            </option>
                                            <option value=""></option>
                                            <option value=""></option>
                                            <option value=""></option>
                                            <option value=""></option>
                                            <option value="***">***</option>
                                        </select>
                                    </div>
                                    {editedItem.isHoliday !== "" && (
                                        <div className="form-group">
                                            <label>  .</label>
                                            <input
                                                name="conversionDate"
                                                value={editedItem.memo}
                                                onChange={handleChange}
                                                required
                                                className="input"
                                            />
                                        </div>
                                    )}
                                </div>
                            </div>
                            <div className="card-footer">
                                <button type="button" className="btn btn-primary me-4" onClick={handleClick}>
                                    
                                </button>

                                <button type="button" className="btn btn-danger" onClick={handleClick}>
                                    
                                </button>
                            </div>
                        {/*</form>*/}
                    </div>
            </div>
    );
}
export default WorkScheduleDashboard;