import React, { createContext, useContext, useState, useEffect } from "react";

//      Context 
const AuthContext = createContext({
  isLoggedIn: false,
  token: null,
  role: null,
  username: null,
  login: () => {},
  logout: () => {},
});

export const useAuth = () => useContext(AuthContext);

export const AuthProvider = ({ children }) => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [token, setToken] = useState(null); // token  
  const [loading, setLoading] = useState(true);
  const [username, setUsername] = useState(null);
  const [role, setRole] = useState(null);
  const [expirationTime, setExpirationTime] = useState(null);

  useEffect(() => {
    const savedToken = localStorage.getItem("token");
    const savedRole = localStorage.getItem("role");
    const savedUsername = localStorage.getItem("username");
    const savedExpirationTime = localStorage.getItem("expirationTime");

    if (savedToken && savedExpirationTime) {
      const currentTime = new Date().getTime();
      if (currentTime < parseInt(savedExpirationTime)) {
        setIsLoggedIn(true);
        setToken(savedToken);
        setRole(savedRole);
        setUsername(savedUsername);
        setExpirationTime(savedExpirationTime);
      } else {
        logout();
      }
    }
    setLoading(false);
  }, []);

  const login = (newToken, newRole, newUsername) => {
    const expirationTime = new Date().getTime() + 3600 * 1000; // 1 
    localStorage.setItem("token", newToken); //  
    localStorage.setItem("role", newRole); //  
    localStorage.setItem("username", newUsername); //   
    localStorage.setItem("expirationTime", expirationTime); //   
    setIsLoggedIn(true);
    setToken(newToken);
    setRole(newRole);
    setUsername(newUsername);
    setExpirationTime(expirationTime);
  };

  const logout = () => {
    setIsLoggedIn(false);
    setToken(null);
    setRole(null);
    setUsername(null);
    setExpirationTime(null);

    localStorage.removeItem("token"); //  
    localStorage.removeItem("expirationTime"); //   
    localStorage.removeItem("role");
    localStorage.removeItem("username");
  };

  useEffect(() => {
    const checkTokenExpiration = () => {
      if (expirationTime) {
        const timeLeft = parseInt(expirationTime, 10) - new Date().getTime();
        if (timeLeft <= 0) {
          logout();
        } else {
          setTimeout(logout, timeLeft); //     
        }
      }
    };

    checkTokenExpiration(); //  

    return () => clearTimeout(); // 
  }, [expirationTime]);

  if (loading) {
    return <div>Loading...</div>; //     
  }

  return (
      <AuthContext.Provider value={{ isLoggedIn, token, role, username, login, logout }}>
        {children}
      </AuthContext.Provider>
  );
};
